<template>
  <v-container fluid id="rules-list">
    <v-container fluid class="grey lighten-3">
      <v-layout row>
        <v-flex md4 offset-md2 class="py-4">
          <div class="headline">Data Aturan</div>
          <div class="subtitle">Aturan yang mempengaruhi perhitungan metode Mamdani.</div>
          <v-btn to="/app/add-rule" dark color="indigo">
            Tambah
          </v-btn>
        </v-flex>
      </v-layout>
    </v-container>
    <v-data-table
      :headers="tableHeaders"
      :items="items"
      :total-items="items.length"
      hide-actions
    >
      <template v-slot:items="props">
        <td>{{props.index + 1 }}</td>
        <td>{{props.item.suhu}}</td>
        <td>{{props.item.tekananUdara}}</td>
        <td>{{props.item.kecepatanAngin}}</td>
        <td>{{props.item.kelembapan}}</td>
        <td>{{props.item.keterangan}}</td>
        <td>
          <v-menu offset-y>
            <template v-slot:activator="{ on }">
              <v-icon v-on="on">more_horiz</v-icon>
            </template>
            <v-list dense>
              <v-list-tile @click="openEditView(props.item._id)">
                <v-list-tile-sub-title>Edit</v-list-tile-sub-title>
              </v-list-tile>
              <v-list-tile @click="deleteData(props.item._id)">
                <v-list-tile-sub-title>Delete</v-list-tile-sub-title>
              </v-list-tile>
            </v-list>
          </v-menu>
        </td>
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import tableHeaders from "./tableHeaders";
import { Rule__find, Rule__delete } from "@/services/rules";
import evbus from "@/evbus";

export default {
  name: "RuleList",
  data: () => ({
    tableHeaders,
    items: []
  }),
  methods: {
    async loadData() {
      evbus.$emit("loading-on");
      try {
        const items = await Rule__find();
        this.items = items;
        evbus.$emit("success", "Sukses memuat data");
      } catch (err) {
        evbus.$emit("error", "Gagal memuat data");
      } finally {
        evbus.$emit("loading-off");
      }
    },
    openEditView(id) {
      this.$router.push(`/app/edit-rule/${id}`);
    },
    deleteData(id) {
      const message = "Apakah anda yakin akan menghapus data #" + id;
      const action = async () => {
        try {
          await Rule__delete(id);
          evbus.$emit("success", "Sukses menghapus data");
        } catch (err) {
          evbus.$emit("error", "Gagal menghapus data");
        } finally {
          evbus.$emit("loading-off");
          this.loadData();
        }
      };
      evbus.$emit("app-action", { message, action });
    }
  },
  mounted() {
    this.loadData();
  }
}
</script>

