import axios from "./axios";
import { Rule } from "@/models/Rule";

export async function Rule__create(payload: Rule) {
  const response = await axios.post("/rules", payload);
  return response.data;
}

export async function Rule__find() {
  const response = await axios.get('/rules');
  return response.data;
}

export async function Rule__findOne(id: string) {
  const response = await axios.get(`/rules/${id}`);
  return response.data;
}

export async function Rule__update(id: string, payload: Rule) {
  const response = await axios.put(`/rules/${id}`, payload);
  return response.data;
}

export async function Rule__delete(id: string) {
  await axios.delete(`/rules/${id}`);
}
